.selectTrendMenu, .selectTrend, input[type=text],
input[type=number],
textarea, .iconBox {
  border: 1px solid #e1e8ee;
}

.kpiButtonBox:focus {
  border: 2px solid #0aa9ec;
}

.kpiButtonAddElement {
  border: 1px dashed #9eb0be;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}

body, h1, h2, h3, h4, p,
figure, blockquote, dl, dd {
  margin-block-end: 0;
}

li, ol, ul {
  list-style: none;
}

body {
  min-height: 100vh;
}

h1, h2,
h3, h4 {
  text-wrap: balance;
}

a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
  color: currentColor;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input, button,
textarea, select {
  font: inherit;
}

textarea:not([rows]) {
  min-height: 10em;
}

:target {
  scroll-margin-block: 5ex;
}

a {
  text-decoration: none;
}

button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: none;
  color: inherit;
  font: inherit;
  line-height: normal;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  width: 1170px;
}

.containerKpiArea {
  max-width: 1512px;
  margin: 0 64px;
}

.mainContainer {
  background: #fff;
  padding: 0px 24px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.08), 0px 0px 6px 0px rgba(0, 0, 0, 0.02);
}

.flexWrap {
  flex-wrap: wrap;
}

.flex {
  display: flex;
}

.flexCol {
  flex-direction: column;
}

.jcs {
  justify-content: center;
}

.jce {
  justify-content: end;
}

.aic {
  align-items: center;
}

.jcsb {
  justify-content: space-between;
}

.jcsa {
  justify-content: space-around;
}

.aie {
  align-items: end;
}

.tal {
  text-align: center;
}

.w25 {
  width: 25%;
}

.w33 {
  width: 33.33%;
}

.w50 {
  width: 50%;
}

.w75 {
  width: 75%;
}

.w100 {
  width: 100%;
}

.fixContent {
  width: -moz-fit-content;
  width: fit-content;
}

body {
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.BoldText {
  color: #000;
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 140%;
  letter-spacing: -0.006px;
}

h4 {
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #000;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: -0.2px;
}

h5 {
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: #f6f8fb;
  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: -0.01em;
  text-align: left;
}

.gp4 {
  gap: 4px;
}

.gp5 {
  gap: 5px;
}

.gp7 {
  gap: 7px;
}

.gp8 {
  gap: 8px;
}

.gp16 {
  gap: 16px;
}

.gp24 {
  gap: 24px;
}

.gp32 {
  gap: 32px;
}

header {
  background-color: #1b3448;
  padding: 24px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.0784313725);
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.0196078431);
}

.addKpiArea {
  background: #f6f8fb;
  padding: 64px 0;
}

.kpiButtonAddElement {
  width: 246px;
  height: 133px;
  padding: 24px;
  border-radius: 6px;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.08), 0px 0px 6px 0px rgba(0, 0, 0, 0.02);
}

.cardSettings {
  padding: 32px 24px;
}

@media screen and (max-width: 500px) {
  .cardSettings {
    padding: 32px 0;
  }
}
.iconBox {
  border-radius: 6px;
  background: #f6f8fb;
  padding: 8px;
  cursor: pointer;
}

.iconBox:hover {
  border: 1px solid #9eb0be;
}

.iconBox:focus {
  border: 1px solid #0aa9ec;
}

input[type=text],
input[type=number],
textarea {
  padding: 8px 10px;
  align-self: stretch;
  border-radius: 6px;
  background: #fff;
}

textarea {
  min-height: 95px !important;
  max-height: 95px !important;
  max-width: 100%;
  min-width: 100%;
  resize: none;
}

::-moz-placeholder {
  color: #9eb0be;
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: -0.006px;
}

::placeholder {
  color: #9eb0be;
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: -0.006px;
}

.titleCardKpi {
  color: #1b3448;
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: -0.006px;
}

.boldNumberCardKpi {
  color: #1b3448;
  font-variant-numeric: lining-nums proportional-nums ordinal;
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 32px;
  font-style: normal;
  font-weight: 600;
  line-height: 140%;
  letter-spacing: -0.33px;
}

#tooltipText {
  border-radius: 6px;
  background: #1b3448;
  padding: 8px;
  color: #fff;
  display: none;
}

.kpiButtonBox {
  border-radius: 6px;
  background: #f6f8fb;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.08), 0px 0px 6px 0px rgba(0, 0, 0, 0.02);
  width: 246px;
  height: 133px;
  padding: 24px;
  cursor: pointer;
}

.kpiButtonBox:focus {
  border-radius: 6px;
  background: #f6f8fb;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.08), 0px 0px 6px 0px rgba(0, 0, 0, 0.02);
}

.selectDropdown {
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: -0.006px;
}

.selectTrend {
  background: #fff;
  padding: 8px 10px;
  border-radius: 6px;
  cursor: pointer;
}
.selectTrend p {
  color: #9eb0be;
}

.selectTrendMenu {
  padding: 5px;
  cursor: pointer;
  border-radius: 4px;
}
.selectTrendMenu ul li {
  border-radius: 4px;
  padding: 8px;
}
.selectTrendMenu ul li:hover {
  background: #f6f8fb;
}
.selectTrendMenu ul li:active {
  background: #0aa9ec;
}
.selectTrendMenu ul li:active button {
  color: #fff;
}

.trendOption {
  color: #1b3448 !important;
}

.updateButtonKpi {
  border-radius: 6px;
  background: #0aa9ec;
  padding: 8px 14px 8px 10px;
  color: #f6f8fb;
  font-family: "Inter", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 140%;
  letter-spacing: -0.006px;
